import { Component, OnDestroy, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { HttpService } from "../http.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit , OnDestroy{
  EMAIL_PATTERN =
    "^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@" +
    "[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$";

  incorrect: string = "";

  loginObj:any={};

  width: number = 100;
  height: number = 100;
  myStyle: Object = {
    position: "fixed",
    width: "100%",
    height: "100%",
    "z-index": -1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
  };
  myParams: object = {
    particles: {
      number: {
        value: 80,
        density: {
          enable: true,
          value_area: 800,
        },
      },
      color: {
        value: "#fff",
      },
      shape: {
        type: "circle",
        polygon: {
          nb_sides: 7,
        },
        image: {
          src: "img/github.svg",
          width: 100,
          height: 100,
        },
      },
      opacity: {
        value: 0.6,
        random: false,
        anim: {
          enable: false,
          speed: 1,
          opacity_min: 0.1,
          sync: false,
        },
      },
      size: {
        value: 5,
        random: true,
        anim: {
          enable: false,
          speed: 40,
          size_min: 0.1,
          sync: false,
        },
      },
      line_linked: {
        enable: true,
        distance: 150,
        //"color": "#5B566E",
        color: "#fff",
        opacity: 0.5,
        width: 2,
      },
      move: {
        enable: true,
        speed: 4,
        direction: "none",
        random: false,
        straight: false,
        out_mode: "out",
        bounce: false,
        attract: {
          enable: false,
          rotateX: 600,
          rotateY: 1200,
        },
      },
    },
    interactivity: {
      detect_on: "canvas",
      modes: {
        grab: {
          distance: 400,
          line_linked: {
            opacity: 1,
          },
        },
        bubble: {
          distance: 400,
          size: 40,
          duration: 2,
          opacity: 8,
          speed: 3,
        },
        repulse: {
          distance: 200,
          duration: 0.4,
        },
        push: {
          particles_nb: 4,
        },
        remove: {
          particles_nb: 2,
        },
      },
    },
    retina_detect: true,
  };

  constructor( private service:HttpService,
               private router:Router) { }


  ngOnDestroy(): void {
    document.body.className="";
  }

  ngOnInit() {
    document.body.className = "bg_image";
  }

  onSubmit(f){
    let obj={
      email:f.value.email,
      password:f.value.password
    }

    this.service.loginCheck(obj)
    .subscribe((response)=>{
      console.log(response)
      this.loginObj=(<any>response)
      console.log("======>" + this.loginObj.msg);
      if(this.loginObj.msg == "Invalid User"){
          this.incorrect = this.loginObj.msg;
      }else{
        sessionStorage.setItem("username", this.loginObj.user.username);
        this.router.navigate(["/home"])
      }
    })
  }
}
